@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', 'Noto Sans',
    monospace;
}

.navbar {
	background-color: #8DBDC7;
	height: 80px;
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

.menu-bars {
	margin-left: 1rem;
	background: none;
	color:navy;
}

.nav-menu {
	background-color: aliceblue;
	width: 15%;
	height: 100vh;
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 300ms;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.nav-menu.active {
	left: 0;
	transition: 150ms;
}

.nav-text {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 12px 0px 7px 14px;
	list-style: none;
	height: 60px;
	color: black;
}

.nav-title {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 7px 0px 7px 14px;
	list-style: none;
	height: 60px;
	color: black;
	font-size: 1.2vw;
}

.nav-text a {
	text-decoration: none;
	color: black;
	font-size: 1.1vw;
	width: 95%;
	height: 100%;
	align-items: center;
	padding: 0 5px;
	border-radius: 5px;
}

.nav-text a:hover {
	background-color: #FBCCC2;
}

.menu-items {
	list-style: none;
	width: 100%;
}

span {
	margin-left: 18px
}
.datasets {
	align-items: center;
	margin:auto;
	width: 60%;
	justify-content: center;
}

.internalPage {
	padding-top: 25px;
	display: flex;
	align-items: center;
	margin:auto;
	width: 60%;
	justify-content: center;
	text-align: center;
}

.infoCard {
	padding-top: 45px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	margin: auto;
	width: 60%;
}

.joinProject {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	min-height: 50vh;
	width: 50%;
	float:left;
}

.createProject {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	min-height: 50vh;
	width: 50%;
	float:right;
}

.login {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	min-height: 100vh;
}

.resourceForm {	
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	min-height: 50vh;
	width: 50%;
	margin: auto;
}
